.advantages {
	padding-top: toRem(84);
	padding-bottom: toRem(84);
	@media (min-width: $mobile) {
		background-color: var(--white-bg-color);
	}
	@media (max-width: $mobile) {
		padding-top: toRem(27);
		padding-bottom: toRem(27);
	}
	// .advantages__container

	&__container {
	}

	// .advantages__content

	&__content {
		display: grid;
		@include adaptiveValue("gap", 65, 30, 0, $containerWidth, 991.98);
		@media (min-width: $tablet) {
			grid-template-columns: 49.584906% 1fr;
			align-items: end;
		}

		@media (max-width: $tablet) {
			text-align: center;
		}
		&:not(:last-child) {
			padding-bottom: toRem(43);
			@media (max-width: $mobile) {
				padding-bottom: toRem(25);
			}
		}
		&:not(:last-child) {
			margin-bottom: toRem(43);
			@media (max-width: $mobile) {
				padding-bottom: toRem(25);
			}
		}
		@media (max-width: $mobile) {
			gap: toRem(25);
		}
		border-bottom: toRem(1) solid var(--green-color-200);
		&.content {
			.content__title {
				margin-bottom: 0;
			}

			.content__text {
			}
		}
	}

	// .advantages__list

	&__list {
		display: grid;
		gap: toRem(20);
		@media (min-width: $tablet) {
			grid-template-columns: repeat(3, 1fr);
		}
	}

	// .advantages__item

	&__item {
	}
}

.item-advantage {
	display: flex;
	gap: toRem(24);
	align-items: center;
	// .item-advantage__icon

	&__icon {
		background-color: var(--brand-color);
		flex: 0 0 toRem(114);
		width: toRem(114);
		height: toRem(114);
		display: flex;
		justify-content: center;
		align-items: center;
		border-radius: toRem(10);
		@media (max-width: toEm(1024)) {
			flex: 0 0 toRem(100);
			width: toRem(100);
			height: toRem(100);
		}
		img {
			width: toRem(53);
			height: toRem(53);
		}
	}

	// .item-advantage__body

	&__body {
		font-size: toRem(17);
		line-height: 1.3;
		@media (max-width: $tablet) {
			font-size: toRem(22);
		}
	}

	// .item-advantage__title

	&__title {
		font-weight: 500;
		&:not(:last-child) {
			margin-bottom: toRem(10);
		}
		@media (max-width: toEm(500)) {
			font-size: toRem(16);
		}
	}

	// .item-advantage__text

	&__text {
		@media (max-width: toEm(500)) {
			font-size: toRem(15);
		}
	}
	@media (max-width: toEm(500)) {
		gap: toRem(20);
	}
}
