.pagination {
	display: flex;
	justify-content: center;
	align-items: center;
	gap: toRem(14);
	.swiper-pagination-bullet {
		flex: 0 0 toRem(44);
		width: toRem(44);
		height: toRem(3);
		background-color: var(--grey-color);
		transition: background-color 0.3s;
		cursor: pointer;
	}
	.swiper-pagination-bullet-active {
		background-color: var(--brand-color);
	}
}
