.outpatient-treatment {
	padding-top: toRem(60);
	padding-bottom: toRem(60);
	@media (max-width: $mobile) {
		padding-top: toRem(30);
		padding-bottom: toRem(30);
	}
	background-color: var(--green-color-100);
	// .outpatient-treatment__container

	&__container {
		display: grid;
		gap: toRem(40);
		text-align: center;
		@media (max-width: $mobile) {
			gap: toRem(25);
		}
	}

	// .outpatient-treatment__header

	&__header {
		max-width: toRem(686);
		margin: 0 auto;
		&.content {
			.content__title {
			}

			.content__text {
			}
		}
	}

	// .outpatient-treatment__content

	&__content {
		display: grid;
		gap: toRem(40);
		@media (max-width: $mobile) {
			gap: toRem(25);
		}
		&.content {
			.content__title {
			}

			.content__text {
			}
		}
	}
}
.table {
	overflow-x: auto;
	table {
		width: 100%;
		border-collapse: collapse;
	}
	thead {
		tr {
			th {
				&:not(:last-child) {
					border-right: toRem(20) solid var(--green-color-100);
					@media (max-width: $mobile) {
						border-right-width: toRem(7);
					}
				}
				background-color: var(--brand-color);
				color: var(--white-color);
				font-weight: 600;
				font-size: toRem(24);
				text-align: center;
				line-height: 1.6;
				height: 96px;
				padding: toRem(20);
				@media (max-width: $tablet) {
					padding: toRem(10);
				}
				@media (max-width: $mobile) {
					font-size: toRem(15);
				}
			}
		}
		border-bottom: toRem(10) solid var(--green-color-100);
	}
	th,
	td {
	}

	tbody {
		font-size: toRem(18);
		tr {
			td {
				line-height: 1.25;
				padding: toRem(13) toRem(44);

				&:nth-child(1) {
					width: 111px;
					text-align: center;
					font-size: toRem(24);
					padding: toRem(22) toRem(10);
					@media (max-width: $mobile) {
						font-size: toRem(15);
					}
				}

				&:nth-child(2) {
					width: 315px;
				}
				&:nth-child(3) {
					width: 315px;
				}
				&:nth-child(4) {
					width: 315px;
				}

				text-align: left;
				&:not(:last-child) {
					border-right: toRem(20) solid var(--green-color-100);
					@media (max-width: $mobile) {
						border-right-width: toRem(7);
					}
				}

				border-bottom: toRem(10) solid var(--green-color-100);

				&:nth-child(2) {
					background-color: #ddd !important;
				}
				background-color: var(--green-light-color);
				@media (max-width: $tablet) {
					padding: toRem(10);
				}
				@media (max-width: $mobile) {
					font-size: toRem(15);
				}
			}
		}
	}
}
