.hero {
	@media (min-width: $mobile) {
		position: relative;
		&::before {
			content: "";
			position: absolute;
			width: 100%;
			height: 100%;
			top: 0;
			left: 0;
			background: linear-gradient(270deg, rgba(217, 217, 217, 0) 0%, #fff 100%);
		}
	}

	// .hero__container

	&__container {
		display: grid;

		align-items: center;
		gap: toRem(20);
		padding-top: toRem(122);
		@media (min-width: $mobile) {
			min-height: 100vh;
			grid-template-columns: 51.393082% 1fr;
		}
		@media (max-width: $mobile) {
			padding-top: toRem(75);
		}
	}

	// .hero__content

	&__content {
		@media (min-width: $mobile) {
			padding-bottom: toRem(30);
			position: relative;
			z-index: 2;
		}
		--margin: 40px;
		@media (max-width: $tablet) {
			--margin: 20px;
		}
		@media (max-width: $mobileSmall) {
			--margin: 15px;
		}
		&.content {
			.content__title_big {
				&:not(:last-child) {
					margin-bottom: var(--margin);
				}
			}

			.content__text {
				// max-width: toRem(550);
				@include adaptiveValue("font-size", 30, 18, 0, $containerWidth, 767.98);
				&:not(:last-child) {
					margin-bottom: var(--margin);
				}
			}
			form {
				@media (max-width: $mobileSmall) {
					width: 100%;

					.button {
						width: 100%;
					}
				}
			}

			@media (max-width: $mobile) {
				order: 2;
				display: flex;
				flex-direction: column;
				text-align: center;
				justify-content: center;
				align-items: center;
			}
		}
	}

	&__image-wrapper {
		align-self: end;
		@media (max-width: $mobile) {
			position: relative;
			margin: 0 toRem(-20);
			order: 1;
			padding-top: toRem(50);
		}
	}

	// .hero__image

	&__image {
		position: relative;
		z-index: 2;
		padding-bottom: 100%;
		img {
			position: absolute;
			width: 110%;
			height: 100%;
			top: 0;
			left: 0;
			object-fit: cover;
			@media (max-width: $mobile) {
				width: 100%;
			}
		}
		@media (max-width: $mobile) {
			padding-bottom: 87%;
		}
	}
	&__bg {
		position: absolute;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		object-fit: cover;
		z-index: -1;
	}
}
