.testimonials {
	padding-top: toRem(90);
	padding-bottom: toRem(90);
	@media (max-width: $mobile) {
		padding-top: toRem(30);
		padding-bottom: toRem(30);
	}
	// .testimonials__container

	&__container {
		display: grid;

		align-items: center;
		gap: toRem(70);
		@include adaptiveValue("gap", 70, 30, 0, $containerWidth, 767.98);
		@media (min-width: $mobile) {
			grid-template-columns: repeat(2, 1fr);
		}
	}

	// .testimonials__content

	&__content {
		&.content {
			@media (max-width: $mobile) {
				text-align: center;
			}
			.content__title {
				color: var(--brand-color);
			}

			.content__text {
				@include adaptiveValue("font-size", 24, 18, 0, $containerWidth, 767.98);
			}
		}
	}

	// .testimonials__body

	&__body {
		min-width: 0;
		position: relative;
	}

	// .testimonials__slider

	&__slider {
	}

	// .testimonials__wrapper

	&__wrapper {
	}

	// .testimonials__pagination

	&__pagination {
		@media (min-width: $mobile) {
			position: absolute;
			bottom: toRem(-44);
			left: 50%;
			transform: translateX(-50%);
			width: 100%;
			height: toRem(20);
		}
		@media (max-width: $mobile) {
			margin-top: toRem(20);
		}
		// margin-top: toRem(44);
	}
}

.testimonial-slide {
	background-color: var(--brand-color);
	padding: toRem(30) toRem(40);
	border-radius: toRem(10);
	color: var(--white-color);
	position: relative;
	&::before {
		content: "";
		position: absolute;
		bottom: toRem(78);
		right: toRem(38);
		width: percent(111, 511);
		height: percent(87, 93);
		background: url("@img/icons/quote.png") bottom / contain no-repeat;
		@media (max-width: $mobile) {
			bottom: toRem(58);
			width: 15.722114%;
		}
	}
	@media (max-width: $tablet) {
		padding: toRem(20);
	}
	// .testimonial-slide__rating

	&__rating {
		&:not(:last-child) {
			margin-bottom: toRem(33);
			@media (max-width: $mobileSmall) {
				margin-bottom: toRem(10);
			}
		}
	}

	// .testimonial-slide__quote

	&__quote {
		font-size: toRem(24);
		line-height: 1.3;
		position: relative;
		&:not(:last-child) {
			margin-bottom: toRem(45);
			@media (max-width: $mobileSmall) {
				margin-bottom: toRem(30);
			}
		}
		@media (max-width: $tablet) {
			font-size: toRem(20);
		}
		@media (max-width: $mobileSmall) {
			font-size: toRem(16);
		}
	}
}

.rating {
	display: flex;
	align-items: center;
	gap: toRem(3);
	// .rating__item

	&__item {
		flex: 0 0 toRem(20);
		width: toRem(20);
		height: toRem(20);
		@media (max-width: $mobileSmall) {
			flex: 0 0 toRem(15);
			width: toRem(15);
			height: toRem(15);
		}
		img {
			max-width: 100%;
		}
	}
}
.profile {
	display: flex;
	align-items: center;
	gap: toRem(18);
	@media (max-width: $mobileSmall) {
		gap: toRem(10);
	}
	// .profile__avatar

	&__avatar {
		overflow: hidden;
		flex: 0 0 toRem(88);
		width: toRem(88);
		height: toRem(88);
		border-radius: 50%;
		@media (max-width: $mobileSmall) {
			flex: 0 0 toRem(50);
			width: toRem(50);
			height: toRem(50);
		}
	}

	// .profile__name

	&__name {
		font-size: toRem(30);
		font-weight: 500;
		line-height: 1.3;
		@media (max-width: $tablet) {
			font-size: toRem(25);
		}
		@media (max-width: $mobileSmall) {
			font-size: toRem(18);
		}
	}
}
