.footer {
	padding-top: toRem(74);
	padding-bottom: toRem(74);
	background-color: var(--brand-color);
	@media (max-width: $mobile) {
		padding-top: toRem(53);
		padding-bottom: toRem(53);
	}
	color: var(--white-color);
	// .footer__container

	&__container {
		display: grid;
		@include adaptiveValue("gap", 70, 30, 0, $containerWidth, 767.98);
		@media (min-width: $mobile) {
			grid-template-columns: 39.4% 1fr;
		}
		@media (max-width: $mobile) {
			gap: toRem(50);
		}
	}

	// .footer__content

	&__content {
		@media (min-width: $mobile) {
			padding-top: toRem(15);
		}
		&.content {
			.content__title {
				&:not(:last-child) {
					margin-bottom: toRem(27);
					@media (max-width: $tablet) {
						margin-bottom: toRem(20);
					}
				}
			}

			.content__text {
				line-height: 1.5;
				&:not(:last-child) {
					margin-bottom: toRem(20);
				}
			}
			@media (max-width: $mobile) {
				text-align: center;
				form {
					width: 100%;
					.button {
						width: 100%;
						font-weight: 600;
					}
				}
			}
		}
	}
}

