.programs {
	padding-top: toRem(90);
	padding-bottom: toRem(90);
	@media (max-width: $mobile) {
		padding-top: toRem(37);
		padding-bottom: toRem(52);
	}
	// .programs__container

	&__container {
	}

	// .programs__content

	&__content {
		&:not(:last-child) {
			margin-bottom: toRem(40);
		}
		&.content {
			.content__title {
			}
		}
	}

	// .programs__list

	&__list {
		display: grid;
		@include adaptiveValue("gap", 63, 20, 0, $containerWidth, 767.98);
		&:not(:last-child) {
			margin-bottom: toRem(40);
			@media (max-width: $mobile) {
				margin-bottom: toRem(30);
			}
		}
		@media (min-width: toEm(601)) {
			grid-template-columns: repeat(2, 1fr);
		}
		@media (max-width: toEm(600)) {
			// gap: toRem(30);
		}
	}

	// .programs__item

	&__item {
	}

	// .programs__text

	&__text {
		padding: toRem(28) toRem(63);
		text-align: center;
		line-height: 1.7;
		font-size: toRem(24);
		background-color: var(--brand-color);
		color: var(--white-color);
		@media (max-width: $mobile) {
			padding: toRem(20);
		}
		@media (max-width: toEm(600)) {
			margin: 0 toRem(-20);
		}
		@media (max-width: $mobileSmall) {
			font-size: toRem(16);
		}
	}
}

.item-program {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	// .item-program__picture

	&__picture {
		width: 100%;
		padding-bottom: percent(418, 584);
		&::before {
			content: "";
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: 87.598%;
			height: 122.388%;
			border-radius: 50%;
			z-index: -1;
		}
		&:not(:last-child) {
			margin-bottom: toRem(15);
		}
	}

	// .item-program__link

	&__link {
	}

	form {
		width: 100%;
		display: flex;
		justify-content: center;
		.button {
			font-size: toRem(32);
			font-weight: 600;
			letter-spacing: toRem(0.32);
			width: 65%;
			margin: 0 auto;
			@media (max-width: $mobile) {
				font-size: toRem(18);
				padding-top: toRem(14);
				padding-bottom: toRem(14);
			}
		}
	}
}
