.about {
	// .about__container

	&__container {
		display: grid;

		@media (min-width: $mobile) {
			grid-template-columns: repeat(2, 1fr);
		}
		@media (max-width: $mobile) {
			gap: toRem(15);
		}
	}

	// .about__content

	&__content {
		display: flex;
		flex-direction: column;
		justify-content: center;
		height: 100%;
		padding: toRem(70) toRem(28) toRem(70) toRem(59);
		background-color: var(--brand-color);
		align-self: center;
		@media (min-width: $mobile) {
			border-radius: toRem(10) 0 0 toRem(10);
		}
		&.content {
			.content_white {
			}

			.content__title {
				&:not(:last-child) {
					margin-bottom: toRem(57);
					@media (max-width: $tablet) {
						margin-bottom: toRem(20);
					}
				}
				&_big {
				}
			}

			.content__text {
			}
		}
		@media (max-width: $tablet) {
			padding: toRem(50) toRem(28) toRem(50) toRem(20);
		}
		@media (max-width: $mobileSmall) {
			padding: toRem(15);
		}
	}

	// .about__image

	&__image {
		border-radius: 0 toRem(10) toRem(10) 0;
		overflow: hidden;
		padding-bottom: percent(493, 616);
	}
}

