.faq {
	padding-top: toRem(45);
	padding-bottom: toRem(92);
	@media (max-width: $mobile) {
		padding-bottom: toRem(52);
		padding-top: toRem(32);
	}
	// .faq__container

	&__container {
		display: grid;

		gap: toRem(54);
		@media (min-width: $mobile) {
			grid-template-columns: 54.3% 1fr;
		}
	}

	&__body {
	}

	// .faq__content

	&__content {
		&:not(:last-child) {
			margin-bottom: toRem(37);
			@media (max-width: $mobile) {
				margin-bottom: toRem(25);
			}
		}
		&.content {
			.content__title {
			}
			.content__text {
			}
		}
	}

	// .faq__spollers

	&__spollers {
	}

	&__image {
		align-self: end;
		padding-bottom: percent(703, 543);
		position: relative;
		&::before {
			content: "";
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			height: percent(468, 703);
			z-index: -1;
			border-radius: toRem(137) toRem(137) 0 toRem(137);
			background-color: #20c3ae;
		}
		@media (max-width: $mobile) {
			&:not(:last-child) {
				margin-bottom: toRem(25);
			}
		}
	}
}

.spollers {
	display: grid;
	gap: toRem(20);
}
// Ð¡Ð¿Ð¾Ð¹Ð»ÐµÑ€
.spollers__item {
	font-size: toRem(20);
	border-radius: toRem(8);
	overflow: hidden;
}
// Ð—Ð°Ð³Ð¾Ð»Ð¾Ð²Ð¾Ðº ÑÐ¿Ð¾Ð¹Ð»ÐµÑ€Ð°
.spollers__title {
	display: flex;
	justify-content: space-between;
	align-items: center;
	@include adaptiveValue("gap", 70, 10, 0, $containerWidth, 767.98);
	width: 100%;
	cursor: pointer;
	text-align: left;
	padding: toRem(20) toRem(44);
	position: relative;
	list-style: none;
	@include adaptiveValue("font-size", 24, 18, 0, $containerWidth, 767.98);
	font-weight: 500;
	line-height: 1.3;
	color: var(--white-color);
	background-color: var(--brand-color);
	transition: background-color 0.3s;
	@media (max-width: $tablet) {
		padding: toRem(20);
	}
	@media (any-hover: hover) {
		&:hover {
			background-color: var(--hover--color);
		}
	}
	&::marker,
	&::-webkit-details-marker {
		display: none;
	}
	@media (max-width: $mobile) {
		padding: toRem(10) toRem(20) toRem(10) toRem(22);
		gap: toRem(5);
	}
	@media (max-width: toEm(600)) {
		font-size: toRem(16);
	}

	&._spoller-active {
		background-color: var(--brand-color);
		.spollers__icon {
			transform: rotate(90deg);
		}
	}
}

.spollers__icon {
	flex: 0 0 toRem(26);
	width: toRem(26);
	height: toRem(26);
	background: url("@img/icons/arrow.png") right / contain no-repeat;
	transition: transform 0.3s;
}
// ÐšÐ¾Ð½Ñ‚ÐµÐ½Ñ‚ ÑÐ¿Ð¾Ð¹Ð»ÐµÑ€Ð°
.spollers__body {
	padding: toRem(16) toRem(22) toRem(15) toRem(44);
	background-color: var(--green-color-100);
	line-height: 1.5;
	@include adaptiveValue("font-size", 19, 16, 0, $containerWidth, 767.98);
	@media (max-width: $tablet) {
		padding: toRem(14) toRem(20);
	}
}
